/* -------------------------------------------------------------------------- 
	#. EDD Mini Cart Header Element
   -------------------------------------------------------------------------- */
.xe-header-element.is-edd-minicart {
  position: relative;
  cursor: pointer;
}
.xe-header-element.is-edd-minicart .xe-edd-minicart-outer {
  display: inline-flex;
  line-height: 1;
}
.xe-header-element.is-edd-minicart .xe-edd-minicart-outer #xe-edd-minicart-button {
  position: relative;
  justify-content: center;
  align-items: center;
  font-size: 12px;
  font-weight: 600;
  border-width: 2px;
  border-style: solid;
  border-color: var(--xe_primary_color);
  border-radius: 0 0 3px 3px;
  width: 28px;
  height: 28px;
  cursor: pointer;
  color: var(--xe_primary_color);
  background-color: transparent;
  transition: all 0.3s ease;
}
.xe-header-element.is-edd-minicart .xe-edd-minicart-outer #xe-edd-minicart-button::before {
  content: "";
  position: absolute;
  left: 50%;
  right: auto;
  top: -10px;
  height: 15px;
  width: 15px;
  border-width: 2px 2px 0 2px;
  border-color: var(--xe_primary_color);
  border-style: solid;
  border-radius: 50% 50% 0 0;
  transform: translateX(-50%);
  transition: all 0.3s ease;
}
.xe-header-element.is-edd-minicart .xe-edd-minicart-outer #xe-edd-minicart-button .items-count {
  line-height: 1;
}
.xe-header-element.is-edd-minicart .xe-edd-cart-items {
  position: absolute;
  z-index: 100000;
  top: calc(100% + 10px);
  right: 0;
  left: auto;
  width: 300px;
  margin: 0;
  padding: 20px;
  color: var(--xe_edd_mini_cart_text_color);
  border: 1px solid var(--xe_edd_mini_cart_border_color);
  border-radius: var(--xe_edd_mini_cart_border_radius);
  background-color: var(--xe_edd_mini_cart_background_color);
  box-shadow: 0 10px 36px -3px rgba(0, 0, 0, 0.12);
  opacity: 0;
  visibility: hidden;
  transition: all 0.3s ease;
}
.xe-header-element.is-edd-minicart .xe-edd-cart-items::after {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 10px;
  transform: translateY(-100%);
}
.xe-header-element.is-edd-minicart .xe-edd-cart-items .empty {
  display: block;
  margin: 0;
  padding: 0;
  font-size: calc(var(--xe_base_font_size) - 15%);
  font-weight: normal;
}
.xe-header-element.is-edd-minicart:hover .xe-edd-cart-items, .xe-header-element.is-edd-minicart:focus .xe-edd-cart-items {
  opacity: 1;
  visibility: visible;
  animation: xeFadeInUp 0.3s 0s ease-in;
}
.xe-header-element.is-edd-minicart .edd-cart {
  display: block;
  list-style: none;
  margin: 0;
  padding: 0;
}
.xe-header-element.is-edd-minicart .edd-cart li {
  display: block;
  width: 100%;
  border-bottom: 1px solid var(--xe_edd_mini_cart_item_seperator_color);
  margin-bottom: 10px;
  padding-bottom: 10px;
}
.xe-header-element.is-edd-minicart .edd-cart .edd-cart-item {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: flex-start;
  justify-content: space-between;
  padding-bottom: 15px;
}
.xe-header-element.is-edd-minicart .edd-cart .edd-cart-item .item-summary {
  flex: 1;
  padding-right: 20px;
}
.xe-header-element.is-edd-minicart .edd-cart .edd-cart-item .item-summary .edd-cart-item-title {
  display: block;
  font-size: calc(var(--xe_base_font_size) - 15%);
  line-height: 21px;
  font-weight: normal;
  margin: 0 0 5px 0;
}
.xe-header-element.is-edd-minicart .edd-cart .edd-cart-item .item-summary .edd-terms-notice {
  display: block;
  font-size: calc(var(--xe_base_font_size) - 20%);
  font-weight: normal;
  font-style: italic;
  line-height: 1.3em;
}
.xe-header-element.is-edd-minicart .edd-cart .edd-cart-item .item-summary .edd-terms-notice .eddr-notice {
  text-align: left;
}
.xe-header-element.is-edd-minicart .edd-cart .edd-cart-item .action {
  width: 25px;
}
.xe-header-element.is-edd-minicart .edd-cart .edd-cart-item .action .xe-edd-remove-from-cart {
  align-items: center;
  justify-content: center;
  width: 25px;
  height: 25px;
  fill: var(--xe_edd_mini_cart_remove_item_color);
  border: 1px solid var(--xe_edd_mini_cart_remove_item_color);
  border-radius: 100%;
}
.xe-header-element.is-edd-minicart .edd-cart .edd-cart-item .action .xe-edd-remove-from-cart svg {
  width: 18px;
  height: 18px;
}
.xe-header-element.is-edd-minicart .edd-cart .edd-cart-item .action .xe-edd-remove-from-cart:hover {
  fill: var(--xe_edd_mini_cart_remove_item_hover_color);
  border-color: var(--xe_edd_mini_cart_remove_item_hover_color);
}
.xe-header-element.is-edd-minicart .edd-cart .edd-cart-total {
  font-size: calc(var(--xe_base_font_size) - 15%);
  text-align: center;
}
.xe-header-element.is-edd-minicart .edd-cart .edd-cart-total .edd-cart-total-price {
  font-weight: bold;
  color: var(--xe_edd_mini_cart_subtotal_amount_color);
}
.xe-header-element.is-edd-minicart .edd-cart .edd-checkout {
  margin-top: 20px;
  padding-bottom: 0;
